<template>
<transition name="appear" appear>
  <div id="MainContent">
    <p class="welcome">WELCOME</p>
    <div class="inputContent">
      <img src="../assets/images/ZhangHao.svg" class="image">
      <input type="text" placeholder="请输入账号" v-model="account">
    </div>
    <div class="inputContent" :id="passwordId?'':'red'">
      <img src="../assets/images/MiMa.svg" class="image">
      <input type="password" placeholder="请输入密码" v-model="password">
    </div>
    <button :class="[(password!=''&&account!='')?'buttonClassY':'buttonClassN']" @click="login()">登录</button>
    <div style="display:flex;margin-top:1vh;"><p class="logon">没有账号?点击</p><p class="logon" style="color:blue;cursor:pointer;" @click="this.$router.push({path:'/Logon'})">注册!</p></div>
  </div>
</transition>
</template>

<script>
export default {
  name:"MainContent",
  data(){
    return{
      account:"",
      password:"",
      passwordId:1
    }
  },
  methods:{
    login(){
      fetch("http://101.201.36.172:520/users/login/" + this.account + "/" + this.password,
      {method:"GET"})
      .then(res=>res.json())
      .then((res)=>{
        if (res.flag == true && res.data != {}) {
            localStorage.setItem("userId", this.account);
            localStorage.setItem("gender",res.data.gender);
            localStorage.setItem("major",res.data.major);
            localStorage.setItem("campus",res.data.campus);
            localStorage.setItem("qq",res.data.qq);
            localStorage.setItem("wechat",res.data.wechat);
            localStorage.setItem("nickName",res.data.nickname);
            localStorage.setItem("image",res.data.heading)
            console.log(res);
            this.$router.push({ path: "Homepage" });
          } 
        else{
            this.passwordId=0;
            setTimeout(()=>this.passwordId=1,1000);
        }
      });
    }
  },
  mounted(){
    document.getElementById("app").style.backgroundImage="linear-gradient(#F3F3F3,#F3F3F3)";
  }
}
</script>

